/* Header styles */
.header {
    background-color: #282c34;
    color: white;
    padding: 10px 0;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
  }
  
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .logo {
    display: flex;
    align-items: center;
  }
  
  .logo img {
    height: 50px;
    margin-right: 10px;
  }
  
  .logo h1 {
    font-size: 1.5em;
    margin: 0;
  }
  
  .nav ul {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  
  .nav ul li {
    margin: 0 10px;
  }
  
  .nav ul li a {
    color: white;
    text-decoration: none;
    font-size: 1.1em;
    transition: color 0.3s ease;
  }
  
  .nav ul li a:hover {
    color: #61dafb;
  }
  
  /* Responsive styles */
  @media (max-width: 768px) {
    .nav ul {
      display: none;
    }
  
    .nav.active ul {
      display: flex;
      flex-direction: column;
      position: absolute;
      background-color: #282c34;
      top: 60px;
      right: 20px;
      width: 200px;
      padding: 10px;
    }
  
    .nav ul li {
      margin: 10px 0;
    }
  
    .nav ul li a {
      font-size: 1.2em;
    }
  }
  